/* Top Navigation Bar */
.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #2c3e50;
  padding: 12px 20px;
  color: white;
  flex-wrap: wrap;
}

.dashboard-title {
  font-size: 24px;
  font-weight: bold;
  flex: 1;
  text-align: left;
}

/* Navigation Buttons - inline and underlined */
.nav-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
  flex: 3;
  margin-left: 90px;
}

.nav-buttons button {
  background: none;
  border: none;
  color: white;
  font-size: 16px;
  text-decoration: underline;
  cursor: pointer;
}

.nav-buttons button:hover {
  color: #00a8ff;
}

/* Removed .logout from nav, handled inside dropdown */

/* Profile section (top right) */
.profile-section {
  position: relative;
  display: flex;
  align-items: center;
}

.profile-circle {
  font-size: 24px;
  cursor: pointer;
}

/* Profile Dropdown */
.profile-dropdown {
  position: absolute;
  top: 40px;
  right: 0;
  background-color: #ffffff;
  color: #333333;
  border: 1px solid #cccccc;
  border-radius: 6px;
  padding: 10px;
  width: 220px;
  z-index: 1000;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.profile-dropdown p {
  margin: 6px 0;
  font-size: 14px;
}

.profile-dropdown button {
  margin-top: 10px;
  width: 100%;
  background-color: #ff6b6b;
  border: none;
  color: white;
  padding: 8px;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

.profile-dropdown button:hover {
  background-color: #e65050;
}

/* Body content */
.dashboard-body {
  padding: 20px;
}

/* Section layout */
.management-sections {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

.management-card {
  flex: 1;
  min-width: 300px;
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 16px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

/* Loading screen */
.loading-screen {
  font-size: 24px;
  text-align: center;
  padding: 50px;
}

/* Tablet: Below 1024px */
@media (max-width: 1024px) {
  .nav-buttons {
    margin-left: 40px;
    gap: 12px;
  }
}

/* Medium devices: Below 768px */
@media (max-width: 768px) {
  .top-bar {
    flex-direction: column;
    align-items: flex-start;
  }

  .dashboard-title {
    text-align: center;
    width: 100%;
    margin-bottom: 10px;
  }

  .nav-buttons {
    flex-direction: column;
    margin: 0 auto;
    gap: 10px;
    width: 100%;
    align-items: center;
  }

  .profile-section {
    align-self: flex-end;
    margin-top: 10px;
  }

  .management-card {
    min-width: 100%;
  }
}

/* Small devices: Below 480px */
@media (max-width: 480px) {
  .top-bar {
    padding: 10px;
  }

  .dashboard-title {
    font-size: 20px;
  }

  .nav-buttons button {
    font-size: 14px;
  }

  .profile-circle {
    font-size: 20px;
  }

  .profile-dropdown {
    width: 180px;
    font-size: 12px;
  }

  .profile-dropdown button {
    padding: 6px;
    font-size: 13px;
  }

  .dashboard-body {
    padding: 10px;
  }
}